.doctors {
	padding: 40px 0 120px 0;
	@include mq($max-width: 1650px) {
		padding: 40px 0 80px; }
	.block-text {
		margin: 15px 0 0 0; }
	.slider {
		margin: 50px 0 0 -12px;
		width: calc( 100% + 21px ); }
	.slide {
		background: white;
		border-radius: 11px;
		box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
		position: relative;
		padding: 54px 40px 20px;
		margin: 12px;
		.exp {
			position: absolute;
			left: 20px;
			top: 20px;
			font-family: 'PF', sans-serif;
			&__title {
				width: 60px;
				height: 60px;
				border-right: 100%;
				background: white;
				box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.06);
				position: relative;
				@include flex(row, start, center, wrap);
				border-radius: 100%;
				padding: 0 0 0 9px;
				line-height: 1;
				b {
					font-size: 27px; }
				span {
					font-size: 13px;
					color: #ACACAC;
					position: absolute;
					top: 50%;
					right: 2px;
					width: 11px;
					transform: rotate(-90deg) translate(0, -50%); } }
			&__text {
				margin: 5px 0 0 0;
				text-align: center;
				font-size: 13px; } }
		&__img {}
		&__name {
			font-family: 'PF', sans-serif;
			text-align: center;
			font-size: 17px;
			margin: 7px 0 11px 0; }
		&__text {
			font-family: 'UniBook', sans-serif;
			text-align: center;
			color: #6B6B6B;
			font-size: 13px;
			margin: 0 0 25px 0; }
		&__reviews {
			margin: 0 0 0 auto;
			text-align: right;
			color: $blue;
			font-weight: 700;
			font-size: 12px;
			position: relative;
			&:before {
				content: '';
				vertical-align: middle;
				display: inline-block;
				width: 14px;
				height: 14px;
				margin: 0 5px 0 0;
				@include bg(null, null, 'icons/talk-icon.svg', no-repeat, center, center, contain); } } }

	.arrow {
		position: absolute;
		top: 50%;
		z-index: 3;
		&-prev {
			left: -80px;
			transform: translate(0, -50%) rotate(180deg);
			@include mq($max-width: 1650px) {
				left: 0; } }
		&-next {
			right: -80px;
			transform: translate(0, -50%);
			@include mq($max-width: 1650px) {
				right: 0; } } } }
