// main page elements
html {
	overflow-x: hidden; }
body {
	font-size: 18px;
	color: black;
	font-family: 'Uni', sans-serif;
	font-weight: 400;
	background: $body-bg;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto; }

.container {
	max-width: 1170px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }

.no-page {
	margin: 150px 0;
	.action {
		max-width: 350px; } }


.btn-callback {
	font-weight: 700;
	border-radius: 43px;
	padding: 12px 32px;
	color: white;
	@include flex(row, null, center, nowrap);
	background: $btn-callback-gradient;
	cursor: pointer;
	svg {
		margin: 0 12px 0 0;
		path {
			fill: white; } } }
.btn-entry {
	font-weight: 700;
	border-radius: 43px;
	padding: 12px 32px;
	color: white;
	@include flex(row, null, center, nowrap);
	background: $btn-entry-gradient;
	cursor: pointer;
	svg {
		margin: 0 12px 0 0;
		path {
			fill: white; } } }

.submit {
	text-align: center;
	color: white;
	background: $btn-submit-gradient;
	padding: 18px 55px;
	font-weight: 700;
	border-radius: 5px;
	font-size: 21px;
	cursor: pointer; }

.block-title {
	font-weight: 100;
	font-size: 35px;
	color: black;
	line-height: 1;
	span {
		font-size: 38px;
		font-style: italic;
		font-family: 'PF', sans-serif; } }

.block-text {
	font-family: 'UniBook', sans-serif;
	line-height: 1.7; }

.logo {
	@include flex(row, null, center, wrap);
	&__img {
		display: block;
		max-width: 141px;
		width: 100%; }
	&__text {
		color: black;
		font-size: 14px;
		padding: 0 0 0 16px;
		margin: 0 0 0 16px;
		border-left: 1px solid #000; } }

.entry-form {
	&.horizontal {
		@include flex(row, null, null, nowrap);
		.entry-form__btn {
			position: relative;
			z-index: 2;
			margin-left: -20px; } }
	&.vertical {
		width: 282px;
		@include flex(column, null, null, nowrap);
		.entry-form-label {
			margin: 0 0 12px 0;
			width: 100%; }
		.entry-form__input {
			width: 100%; }
		.entry-form__btn {
			width: 100%;
			margin: 0; } }
	&-label {
		position: relative;
		svg {
			display: block;
			width: 23px;
			position: absolute;
			top: 50%;
			left: 25px;
			z-index: 2;
			transform: translate(0, -50%);
			path {
				fill: $brown; } } }
	&__input {
		background: rgba(255, 255, 255, .5);
		padding: 19.5px 72px;
		border-radius: 6px;
		border: 3px solid $brown;
		width: 311px;
		font-family: 'UniBook', sans-serif;
		color: $brown;
		font-size: 18px;
		@include mq($max-width: 1450px) {
			width: 285px; }
		&::placeholder {
			font-family: 'UniBook', sans-serif;
			font-size: 18px;
			color: $brown; } } }

.arrow {
	background: $gray;
	border-radius: 100%;
	width: 60px;
	height: 60px;
	@include flex(row, center, center, nowrap);
	position: absolute;
	top: 50%;
	cursor: pointer;
	@include trs;
	&:hover {
		background: $btn-callback-gradient;
		svg {
			width: 24px; } }
	svg {
		@include trs;
		display: block;
		width: 16px;
		path {
			fill: white; } } }
