.about {
	padding: 45px 0 40px;
	color: $dark;
	.container {
		background: white;
		max-width: 1510px;
		@include flex(row, between, null, wrap);
		border-radius: 10px;
		box-shadow: $container-shadow;
		position: relative;
		@include mq($max-width: 1650px) {
			max-width: 1170px; }
		&::before {
			content: '';
			display: block;
			position: absolute;
			border: 43px solid #FAFAFA;
			width: 431px;
			height: 431px;
			border-radius: 100%;
			right: 0;
			top: 0;
			z-index: -1;
			transform: translate(-10%, -40%);
			@include mq($max-width: 1650px) {
				width: 310px;
				height: 310px; } } }
	.block-title {
		margin: 0 0 37px 0;
		@include mq($max-width: 1650px) {
			margin: 0 0 15px 0; } }
	&__img {
		display: block;
		max-width: 641px;
		width: auto;
		border-radius: 10px 0 0 10px;
		@include mq($max-width: 1650px) {
			max-width: 40%; } }
	.text-container {
		margin: 45px 50px;
		width: calc( 100% - 741px );
		overflow: auto;
		font-family: 'UniBook', sans-serif;
		line-height: 1.6;
		@include mq($max-width: 1650px) {
			margin: 30px;
			width: calc(60% - 60px); } } }

